#popups {
    position: absolute;
    top: 0;
    left: 0;
    z-index: 10001;

    #social-media-popup-container {
        position: fixed;
        color: #fff;
        right: 0;
        bottom: 100px;
        display: grid;
        grid-template-columns: auto auto;
        transform: translateX(50%);
        transition: 0.4s;

        &:hover {
            transform: translateX(0%);

            .open-popup {
                p {
                    margin: 10px 0 0 15px;
                    transform: rotate(180deg);
                }
            }
        }

        .open-popup {
            width: 55px;
            height: 60px;
            border-top-left-radius: 50%;
            border-bottom-left-radius: 50%;
            background-color: black;
            display: flex;
            justify-content: center;
            align-items: center;
            
            & > p {
                margin: 0 0 5px 12px;
                font-size: 24px;
                transition: 0.5s;
            }
        }

        .popup-social-list {
            background-color: black;
            border-bottom-left-radius: 10px;

            a {
                color: #fff;
                text-decoration: none;
            }
        }
    }
}